body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

td,
th {
  border: 1px solid #dddddd;
  text-align: left;
  padding: 8px;
}


/* timer styles */
span.time-setter-title {
    display: inline-block;
    min-width: 215px;
}

span.time-setter-title,
span.time-setter-val {
    font-size: 18px;
}

span.time-setter-val {
    display: inline-block;
    min-width: 22px;
    margin: 0 3px;
    text-align: right;
}

span.time-setter-title.disabled,
span.time-setter-val.disabled {
    color: gray;
}

.main-panel span.time-category {
    min-width: 85px;
    display: inline-block;
}

.control-btn-wrapper {
    margin: 15px 0;
}
.control-btn-wrapper button {
    margin: 0 5px;
}
.control-btn-wrapper button:first-of-type,
.control-btn-wrapper button:last-of-type {
    margin: 0;
}

button {
    border: 1px solid black;
    border-radius: 5px;
    text-align: center;
    background-color: #ccdffc;
    display: inline-block;
}

button:active {
    background-color: #91bdff;
}

button:disabled {
    background-color: #dcdcdc;;
}

.control-btn-wrapper > button {
    padding: 12px;
    background-color: #dcdcdc;
    font-size: 16px;  
}

.control-btn-wrapper button.start,
.control-btn-wrapper button.resume {
    background-color: #8dd9a5;
}

.control-btn-wrapper button.pause {
    background-color: #e3e371;
}

.control-btn-wrapper button.reset {
    background-color: #8cc3ed;
}

.control-btn-wrapper button.fast-forward {
    background-color: #a4a5f5;
}

.control-btn-wrapper button.start,
.control-btn-wrapper button.pause,
.control-btn-wrapper button.resume {
    min-width: 86px;
}

.control-btn-wrapper button:disabled {
    background-color: #dcdcdc;
}

.interval-wrapper {
    margin-bottom: 10px;
}

/* fix for mobile devices */
@media (max-width: 420px) {
    span.time-setter-title {
        min-width: 80px;
    }    
}